
import {Box,Stack,Typography,Card, CardMedia, CardContent,Button,AppBar} from '@mui/material'
import { useParams , useNavigate} from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux';
import { Singleproducts, } from '../features/ProductSlice';
function FilteredProducts() {
    const navigate = useNavigate();
    const {type} = useParams();
    const products = useSelector((state)=>state.product.Featureproducts);
    const dispatch = useDispatch();
    const Filter = [
      'size',
      'color',
      'male',
      'female'
    ]
  return (
   <Box>
   <nav>
  <Typography variant='h4' fontWeight='bold' color='grey' m ={2}>
   {type}
  </Typography>
  <Stack spacing={3} direction='row' justifyContent='space-between' alignItems={'center'} m={2} >
  <Stack direction='row' justifyContent='flex-start' alignItems='center' gap={2}>
{
  Filter?.map(item=>(
<Button key ={item} >{item}</Button>
  ))
}
  </Stack>
  <Button>Clear Filter</Button>
  </Stack>
   </nav>
  <Stack direction={'row'} gap={10} flexWrap={'wrap'} justifyContent={'center'} alignItems={'center'}>
  {
    products?.map((item,index)=>{
        return(
            <Card key= {index} sx={{maxWidth:'25em'}} onClick={(e)=>{
              e.preventDefault();
              navigate(`/filteredProducts/${type}/${item.id}`);
              dispatch(Singleproducts(item.id));
            }}>
            <CardMedia
            component='img'
            image = {item.img}
            height='350'
            />
            <CardContent width='50%'>
            <Typography varaint='body2'>{item.name}</Typography>
            <hr />
            <Typography>{item.text}</Typography>
            <hr />
            <Stack direction={'row'} alignItems={'center'} spacing={2} justifyContent='space-between'>
            <Typography variant='subtitle1'>
            price:{item.price}$ size:{item.size?.map((item)=>(<span>{item},</span>))}
            </Typography>
            <Stack direction='row' gap={1}>
                {item.color?.map((item,index)=>{
                       return(
                        <span key={index} style={{backgroundColor:`${item}`,borderRadius:'20px' ,padding:'.3em .8em'}}>
                        </span>
                       )
                    })
                    }
            </Stack>
            </Stack>
            </CardContent>
            </Card>
        )
    })
  }
  </Stack>
   </Box>
  )
}

export default FilteredProducts
