import { createSlice } from "@reduxjs/toolkit"
export const SliderSlice = createSlice({
    name: "slider",
    initialState:{
        value:0,
        lenght:3
    },
    reducers:{
        nextvalue(state,action){
      state.value = action.payload > state.lenght ? 0 : action.payload;
        },
        prevalue(state,action){
       state.value = action.payload < state.lenght-3 ? 0:action.payload;
        }
    }
})
 export const {nextvalue,prevalue} = SliderSlice.actions;
 export default SliderSlice.reducer;