import React from 'react'
import {Box, Button,Stack, Typography} from '@mui/material';
import clothes from '../assets/images/clothes.jpg';
import { useDispatch } from 'react-redux';
import {FeatureProducts} from '../features/ProductSlice'
import {Link} from 'react-router-dom'
const nav  = [
"Hoodies",
"Dresses",
"Suits",
"Shoes",
"T-Shirts",
"Jeans",
"Jackets",
"Bags"
];
function NavigationButton() {
  const dispatch = useDispatch();
  return (
    <Box sx={{m:'2em'}}>
<Stack direction='row' justifyContent='space-around' alignItems='center'>
    {nav.map((item)=>{
return(<Link to = {'/filteredProduct/'+item}>
  <Button sx={{border:'2px solid grey',color:'black'}} fontWeight='bold' onClick={()=>dispatch(FeatureProducts(item))}>{item}</Button>
</Link>)
        })}
</Stack>
<Box sx={{display:'flex',justifyContent:'center'}} mt={3}>
<Stack direction={'column'} justifyContent={'center'} alignItems={'center'}>
  <Typography textAlign={'center'} p={1} bgcolor={'	#009926'} color={'#990000'} variant='h5' sx={{width:'90%'}} borderRadius={2} m= {1}>
  Sales upto 50%
  </Typography>
  <img src={clothes} alt="clothes" style={{width:'60em',height:'70%'}}/>
</Stack>
</Box>
    </Box>
  )
}

export default NavigationButton
