
import { createSlice } from "@reduxjs/toolkit";
import {storeData} from '../assets/data/dummyData'
const ProductSlice = createSlice({
    name:'product',
    initialState:{
        Featureproducts:JSON.parse(localStorage.getItem('product')),
        Singleproducts:JSON.parse(localStorage.getItem('productid')),
    },
    reducers:{
        FeatureProducts(state,action){
        const product = storeData.filter((products)=>products.type === action.payload);
        state.Featureproducts = product;
        const savestore = JSON.stringify(product);
        localStorage.setItem('product',savestore);
        },
        Singleproducts(state,action){
        const product = storeData.filter((product)=>product.id === action.payload);
        state.Singleproducts = product;
        const savep = JSON.stringify(product);
        localStorage.setItem('productid',savep);

        }
    }
})
export const {FeatureProducts,Singleproducts} = ProductSlice.actions;
export default ProductSlice.reducer;