import { Box, MenuItem, Stack,Typography,Select,FormControl, InputLabel, SelectChangeEvent, Tooltip, Button} from '@mui/material';
import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom'
import { addtocart } from '../features/CartSlice';
function Singleproduct() {
    const {id} = useParams();
    const product = useSelector((state)=>state.product.Singleproducts);
    const {name,text,img,gender,price,color,size} = product[0];
    const [Size,setsize] = useState(null);
    const [Color,setColor] = useState(null);
    const handels = (event)=>{
      setsize(event.target.value);
    }
    const handelc = (event)=>{
      setColor(event.target.value);
    }
    const dispatch = useDispatch();
    const addtoc = ()=>{
      dispatch(addtocart({id: id,
        price: price,
        size: Size,
        amount: 1,
        img:img,
        totalPrice:price,
        name:name,
        text:text,
        color:Color}));
    }
  return (
   <>
    <Stack direction='row' alignItems={'center'} justifyContent='space-around'>
      <Box>
     <img src={img} alt={name}  style={{maxHeight:'810px'}}/>
      </Box>
     <Box sx={{maxWidth:'500px'}}>
     <Typography variant='h4'>{name}</Typography>
     <Typography textAlign={'center'} variant='h4' sx={{color:'orange'}}>15% OFF</Typography>
     <Typography varaint='body2' fontWeight={'bold'}>{text}</Typography>
     <Box sx={{minWidth:'120px' , maxWidth:'300px'}} m= {2}>
     <FormControl fullWidth>
     <InputLabel id ='picks'>Pick a Size</InputLabel>
     <Select id= 'picks' value={Size} onChange={handels}>
      {
        size.map((item)=>(
          <MenuItem value={item} key={item}>{item}</MenuItem>
        ))
      }
     </Select>
     </FormControl>
     </Box>
     <Box sx={{minWidth:'120px', maxWidth:'300px'}} m={2}>
     <FormControl fullWidth>
     <InputLabel id ='pickc'>Pick your Color</InputLabel>
     <Select id= 'pickc'
     value={Color}
     onChange={handelc}
     >
      {
        color.map((item)=>(
          <MenuItem value={item} key={item}>{item}</MenuItem>
        ))
      }
     </Select>
     </FormControl>
     </Box>
     <Tooltip title='add to cart'>
      <Button  sx={{color:'grey',border:'2px solid grey', borderRadius:'10px',padding:'20px'}}
      onClick={addtoc}>
        Add to Cart
      </Button>
     </Tooltip> 
     </Box>
    </Stack>
   </>
  )
}

export default Singleproduct