import React from 'react'
import {Box,Button, Stack} from '@mui/material'
import { useDispatch } from 'react-redux'
import { useSelector } from 'react-redux/es/hooks/useSelector'
import { nextvalue, prevalue } from '../features/SliderSlice'
import { sliderData } from '../assets/data/dummyData'
function Slider() {
    const Sliderindex = useSelector((state)=>state.slider.value)
    const dispatch = useDispatch();
  return (
    <>
    <Stack direction='row' justifyContent='space-around' m={2}>
   <Button onClick={()=>dispatch(prevalue(Sliderindex - 1))} disabled={Sliderindex == 0}>
   prev</Button>
   <Box>
{
    sliderData.map((item)=>{
        return(
            <div key={item.id}>
            <Stack direction='column' justifyContent='center' alignItems='center'>
            {item.id == Sliderindex && <img className='sliderimg' src={item.img} alt='slider' style={{height:'20em'}}/>}
            {item.id == Sliderindex && <span classnmae='slidertext' style={{fontSize:'1.5em',fontWeight:'bold'}}>{item.text}</span>}
            </Stack>
            </div>
        )
    })
}
   </Box>
   <Button onClick={()=>dispatch(nextvalue(Sliderindex + 1))}>
next
   </Button>
    </Stack>
    </>
  )
}

export default Slider
