import React from 'react';
import './App.css';
import {BrowserRouter, Routes,Route} from 'react-router-dom'
import Home from './Componet.js/Home';
import FilteredProducts from './Componet.js/FilteredProducts';
import Singleproduct from './Componet.js/Singleproduct';
import { useSelector } from 'react-redux';
function App() {
  const data = useSelector((state)=>state.cart.cart);
  const dataa = useSelector((state)=>state.cart.amount);
  const datat = useSelector((state)=>state.cart.totalAmount);
  const datap = useSelector((state)=>state.cart.totalPrice);
  console.log('cart',data);
  console.log('amount',dataa);
  console.log('totalAmount',datat);
  console.log('totalPice',datap);

  return (
   <BrowserRouter>
<Routes>
  <Route path = '/' exact element = {<Home/>}/>
  <Route path = '/filteredProduct/:type' element = {<FilteredProducts/>}/>
  <Route path = '/filteredProducts/:type/:id' element={<Singleproduct/>}/>
</Routes>
   </BrowserRouter>
  );
}

export default App;
