import React from 'react'
import Navbar from './Navbar'
import Slider from './Slider'
import NavigationButton from './NavigationButton'
import {storeData} from '../assets/data/dummyData'
import {Stack,Typography,Card,CardContent,CardMedia} from '@mui/material'
import { useNavigate } from 'react-router-dom'
import { useDispatch } from 'react-redux';
import { Singleproducts } from '../features/ProductSlice'
function Home() {
    const products = storeData;
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const type = 'landing'
  return (
 <>
    <Navbar/>
    <Slider/>
    <NavigationButton/>
    <Stack direction={'row'} gap={10} flexWrap={'wrap'} justifyContent={'center'} alignItems={'center'}>
  {
    products.map((item,index)=>{
        return(
          <Card key= {index} sx={{maxWidth:'25em'}} onClick={(e)=>{
              e.preventDefault();
              navigate(`/filteredProducts/${type}/${item.id}`);
              dispatch(Singleproducts(item.id));
            }}>
            <CardMedia
            component='img'
            image = {item.img}
            height='350'
            />
            <CardContent width='50%'>
            <Typography varaint='body2'>{item.name}</Typography>
            <hr />
            <Typography>{item.text}</Typography>
            <hr />
            <Stack direction={'row'} alignItems={'center'} spacing={2} justifyContent='space-around'>
            <Typography variant='subtitle1'>
            price:{item.price}$  size:{item.size}
            </Typography>
            <Stack direction='row' gap={1}>
                {item.color.map((item,index)=>{
                       return(
                        <span key={index} style={{backgroundColor:`${item}`,borderRadius:'20px' ,padding:'.3em .8em'}}>
                        </span>
                       )
                    })
                    }
            </Stack>
            </Stack>
            </CardContent>
            </Card>
        )
    })
  }
  </Stack>
 </>
  )
}

export default Home
